import React, { useState, useContext } from "react";
import Card from "../../shared/components/UIElements/Card";
import Input from "../../shared/components/FormElements/Input";
import Button from "../../shared/components/FormElements/Button";
import ErrorModal from "../../shared/components/UIElements/ErrorModal";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";
import ImageUpload from "../../shared/components/FormElements/ImageUpload";

import { useForm } from "../../shared/hooks/form-hook";
import { AuthContext } from "../../shared/components/context/auth-context";
import { useHttpClient } from "../../shared/hooks/http-hook";
import {
  VALIDATOR_EMAIL,
  VALIDATOR_MINLENGTH,
  VALIDATOR_REQUIRE,
} from "../../shared/utils/validators";

import "./Auth.css";

const Auth = () => {
  const auth = useContext(AuthContext);
  const [isLogin, setIsLogin] = useState(true);
  const { isLoading, ErrorMsg, sendRequest, clearError } = useHttpClient();

  const [formState, inputHandler, setFormData] = useForm(
    {
      email: {
        value: "",
        isValid: false,
      },
      password: {
        value: "",
        isValid: false,
      },
    },
    false
  );

  const registerhandler = () => {
    if (!isLogin) {
      setFormData(
        {
          ...formState.inputs,
          name: undefined,
          image: undefined
        },
        formState.inputs.email.isValid && formState.inputs.password.isValid
      );
    } else {
      setFormData(
        {
          ...formState.inputs,
          name: {
            value: "",
            isValid: false,
          },
          image:{
            value: null,
            isValid: false
          }
        },
        false
      );
    }
    setIsLogin((prevMode) => !prevMode);
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    try {
      let responseData;
      if (isLogin) {
        responseData = await sendRequest(
          process.env.REACT_APP_BACKEND_URL + "/users/login",
          "POST",
          JSON.stringify({
            email: formState.inputs.email.value,
            password: formState.inputs.password.value,
          }),
          {
            "Content-Type": "application/json",
          }
        );
        
      } else {
        const formData = new FormData()
        formData.append('email',formState.inputs.email.value)
        formData.append('name',formState.inputs.name.value)
        formData.append('password',formState.inputs.password.value)
        formData.append('image',formState.inputs.image.value)
        responseData = await sendRequest(
          process.env.REACT_APP_BACKEND_URL + "/users/signup",
          "POST",
          formData,
        );
      }
      
      auth.login(responseData.userId,responseData.token);
    } catch (err) {}
  };

  return (
    <React.Fragment>
      <ErrorModal error={ErrorMsg} onClear={clearError} />
      <Card className="authentication">
        {isLoading && <LoadingSpinner asOverlay />}
        <h2>Log In Required</h2>
        <hr />
        <form onSubmit={onSubmit}>
        {!isLogin && <ImageUpload center id="image" onInput={inputHandler} errorText="Please provide an Image"/>}
          {!isLogin && (
            <Input
              id="name"
              element="input"
              type="text"
              label="User Name"
              validators={[VALIDATOR_REQUIRE()]}
              errorText="Please enter your name"
              onInput={inputHandler}
            />
          )}
          
          <Input
            id="email"
            element="input"
            type="email"
            label="E-Mail"
            validators={[VALIDATOR_EMAIL()]}
            errorText="Please add valid email address"
            onInput={inputHandler}
          />
          <Input
            id="password"
            element="input"
            type="password"
            label="Password"
            validators={[VALIDATOR_MINLENGTH(6)]}
            errorText="Please add valid password of 6 characters "
            onInput={inputHandler}
          />
          <Button type="submit" disabled={!formState.isValid}>
            {isLogin ? "Log In" : "Register"}
          </Button>
        </form>
        <Button inverse onClick={registerhandler}>
          Switch to {isLogin ? "Register" : "Log In"}
        </Button>
      </Card>
    </React.Fragment>
  );
};

export default Auth;
